import { useMemo } from "react"
import { useNavigate } from "react-router-dom"
import { BriefConfig } from "@rp/react/tanstack-api/brief"
import { useModal } from "@rp/react/web/common/providers"
import { EditNote, PlaylistRemove } from "@rp/react/web/mui/icons"
import { BRIEF_ROUTE_EDIT } from "@rp/react/web/router"
import { BriefConfigCardActionsDict } from "./briefConfigCardActions.type"

type UseBriefConfigCardActionsState = {
  actionsDict: BriefConfigCardActionsDict
}

const useBriefConfigCardActions = (): UseBriefConfigCardActionsState => {
  const {
    unsubscribeBriefConfigModal: { handleOpen: handleUnsubscribe },
  } = useModal()

  const navigate = useNavigate()

  const actionsDict = useMemo(
    () => ({
      edit: {
        id: "edit",
        label: "Edit Brief",
        icon: <EditNote />,
        onClick: ({ id }: BriefConfig) => navigate(BRIEF_ROUTE_EDIT.replace(":briefId", id)),
      },
      unsubscribe: {
        id: "unsubscribe",
        label: "Unsubscribe",
        icon: <PlaylistRemove sx={{ fill: "#FC5555" }} />,
        onClick: handleUnsubscribe,
        color: "#FC5555",
      },
    }),
    [navigate, handleUnsubscribe],
  )

  return { actionsDict }
}

export { useBriefConfigCardActions }
export type { UseBriefConfigCardActionsState }
